@tailwind base;
@tailwind components;
@tailwind utilities;

body * {
    font-family: 'Shabnam FD!important';
    word-break: break-word;
}


.box {
    width: 80%;
    position: absolute;
    height: 2px;
    border-radius: 10px;
    top: 20%;
    left: 50%;
    transform: translateX(-50%);
    animation: moveBox 2s ease-in-out infinite alternate;
}

@keyframes moveBox {
    from {
        top: 20%;
    }
    to {
        top: 80%;
    }
}


.qr-video-container video {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensures the video covers the square */
}